body {
    font-family: 'Raleway', sans-serif;
    background: #1b1b1d;
}

.container {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #1b1b1d;
    background-image: url('./assets/images/bricks-background.jpeg');
}

.container .cuadro {
    position: relative;
    top: 50%;
    left: 50%;
    width: 60%;
    padding: 40px;
    padding-bottom: 110px;
    text-align: center;
    border: 2px solid #fff;
    transform: translate(-50%, -50%);
}

.container .cuadro h1 {
    font-size: 1rem;
    color: #fff;
}

.container .cuadro h1 span {
    display: block;
}

.container .cuadro h2 {
    margin: 20px 0;
    font-size: 1.4rem;
    color: #fff;
    text-transform: uppercase;
}

.container .cuadro hr {
    height: 2px;
    background-image: -webkit-linear-gradient(left, #1b1b1d, #fff, #1b1b1d);
    border: 0;
}

.container .cuadro h3 {
    color: #fff;
    font-family: inherit;
    text-transform: uppercase;
    margin-top: 20px;
}

.container .cuadro .suscripcion {
    width: 100%;
    height: 80px;
    padding: 2px 0;
    margin: 1.4rem auto 0;
    background: #fff;
    border-radius: 0.2rem;
}

.container .cuadro .suscripcion form #email {
    display: relative;
    width: 90%;
    height: 40px;
    padding: 0px 6px;
    margin: 0;
    vertical-align: top;
    font-family: 'Raleway', sans-serif;
    font-size: 0.9rem;
    color: #000;
    background: #fff;
    border: none;
    outline-style: none;
}

.container .cuadro .suscripcion form #email:disabled {
    background: hsl(0, 0%, 80%);
    cursor: not-allowed;
}

.container .cuadro .suscripcion form #btn {
    width: 98%;
    height: 40px;
    margin: 0;
    padding: 0;
    vertical-align: top;
    background: #3085d6;
    border-radius: 0.2rem;
    border: none;
    outline-style: none;
    cursor: pointer;
    transition: background-image ease 500ms;
}

.container .cuadro .suscripcion form #btn:hover {
    background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1));
}

.container .cuadro .suscripcion form #btn:active {
    background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
}

.container .cuadro .suscripcion form #btn:disabled {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
    cursor: not-allowed;
}

p {
    color: #ff0000;
}

.container .cuadro .suscripcion .error {
    margin: 1.2rem auto 0;
    font-family: inherit;
    text-transform: uppercase;
}

.container .cuadro .social-media {
    width: 100%;
    margin-top: 4rem;
    position: absolute;
    /* top: 50%; */
    left: 50%;
    transform: translate(-50%, -50%);
}

.container .cuadro .social-media a {
    margin: 0 1rem;
    font-size: 1.5rem;
    color: #fff;
}
.container .cuadro .social-media a:hover {
    color: #3085d6;
}

@media screen and (min-width: 488px) {
    .container .cuadro {
        padding: 70px 70px 110px;
    }
    .container .cuadro h1 {
        font-size: 1.4rem;
    }
    .container .cuadro h2 {
        font-size: 2rem;
    }
}

@media screen and (min-width: 600px) {
    .container .cuadro {
        padding: 80px 80px 110px;
    }

    .container .cuadro h1 span {
        display: inline;
    }
    .container .cuadro h2 {
        font-size: 2.4rem;
    }
    .container .cuadro .suscripcion {
        height: 40px;
    }
    .container .cuadro .suscripcion form #email {
        display: inline-block;
        width: 75%;
    }
    .container .cuadro .suscripcion form #btn {
        width: 19%;
    }
}

@media screen and (min-width: 767px) {
    .container .cuadro h2 {
        font-size: 2.6rem;
    }
    .container .cuadro hr {
        height: 3px;
    }
    .container .cuadro .suscripcion {
        width: 60%;
    }
    .container .cuadro .suscripcion form #email {
        width: 72%;
    }
    .container .cuadro .suscripcion form #btn {
        width: 21%;
    }
}

@media screen and (min-width: 950px) {
    .container .cuadro {
        padding: 110px 90px;
    }
    .container .cuadro h2 {
        font-size: 3rem;
    }

    .container .cuadro .suscripcion {
        margin-top: 40px;
    }

    .container .cuadro .suscripcion form #email {
        width: 80%;
    }
    .container .cuadro .suscripcion form #btn {
        width: 15%;
    }
    .social-media {
        margin-top: 50px;
    }
}
